.matador-screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px;
  overflow: hidden;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;
}

.matador-deprecated-notice {
  border: 4px solid orange;
  padding: 1rem;
  margin-bottom: 1rem;
  background-color: #ffc;
  p:last-child {
    margin-bottom: 0;
  }
}

.matador-application,
.matador-form {
  // This is the honeypot field
  .matador-field-job-seeker {
    clip: rect(1px, 1px, 1px, 1px);
    height: 1px;
    overflow: hidden;
    position: absolute !important;
    width: 1px;
    margin: 0;
    padding: 0;
    border: 0;
    word-wrap: normal !important;
  }

  .matador-field-type-file,
  .matador-field-type-files{
    .for-file {
      text-overflow: ellipsis;
      white-space: nowrap;
      cursor: pointer;
      display: inline-block;
      overflow: hidden;
      padding: 10px 20px;
      width: 100%;
      max-width: 100%;
      box-sizing: border-box;

      span {
        display: inline-block;
        margin-left: 6px;
      }

      svg {
        width: 1em;
        height: 1em;
        vertical-align: middle;
        fill: currentColor;
        margin-top: 4px;
        margin-right: 4px;
        display: inline;
      }

      .no-js & {
        display: none;
      }
    }

    .inputfile {
      width: 0;
      height: 0;
      opacity: 0;
      overflow: hidden;
      position: absolute;
      z-index: -1;
      margin-left: -10000px;

      .no-js & {
        width: auto;
        height: auto;
        opacity: 1;
        overflow: auto;
        position: static;
        z-index: 1;
        margin-left: 0;
      }
    }
  }

  #matador-upload-overlay {
    display: none;
    width: 104%;
    height: 104%;
    position: absolute;
    top: -2%;
    left: -2%;

    .matador-upload-overlay-background {
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background: whitesmoke;
      opacity: .6;
      filter: alpha(opacity=70);
      border: 0;
    }

    .matador-upload-overlay-message {
      margin-top: -50%;
      text-align: center;
      border: 1px solid;
      margin-left: 2%;
      margin-right: 2%;
      padding: 40px;
      opacity: 1;
      filter: alpha(opacity=100);
      position: absolute;
    }

    .spinner {
      background: url('../images/spinner.gif') no-repeat;
      -webkit-background-size: 20px 20px;
      background-size: 20px 20px;
      display: inline-block;
      vertical-align: middle;
      opacity: .7;
      filter: alpha(opacity=70);
      width: 20px;
      height: 20px;
      margin: -4px 6px 0;
    }
  }
}